import { StatusBar } from 'expo-status-bar';
import React from 'react';
import { StyleSheet, Text, View, Image } from 'react-native';
import { Button, Checkbox, Provider, RadioButton, Subheading, TextInput } from 'react-native-paper';
import * as Location from 'expo-location'
import * as Linking from 'expo-linking';

import { RecoverIdentity}  from 'centeridentity-expo';
import CenterIdentity from 'centeridentity';
import { Graph, Identity, Crypt, Settings, Transaction } from 'yadacoinjs';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    let data = {}
    if (window.location.hash.length > 1) {
      data = JSON.parse(atob(window.location.hash.substr(1)))
    }
    this.state = {
      graph: new Graph(),
      identity: new Identity(),
      ci: new CenterIdentity(undefined, 'https://centeridentity.com', true),
      checked: true,
      ...data
    }
    document.title = this.state.method
    this.state.graph.settings = new Settings()
    this.state.graph.settings.webServiceURL = window.location.origin
    this.state.graph.identity = this.state.identity
    this.state.graph.crypt = new Crypt()
    this.state.graph.crypt.identity = this.state.graph.identity
    this.state.graph.transaction = new Transaction();
    this.state.graph.transaction.crypt = this.state.graph.crypt
    this.state.graph.transaction.identity = this.state.graph.identity
    this.state.graph.transaction.settings = this.state.graph.settings

    this.state.sign = ['contract', 'signin', 'invite'].indexOf(this.state.method) > -1

    let identities = localStorage.getItem('identities')
    if(identities) {
      identities = JSON.parse(identities)
      this.state.addIdentity = false
    } else {
      identities = []
      this.state.addIdentity = true
    }
    this.state.identities = identities

    if (this.state.message?.identity) {
      this.state.identities
      .filter((identity) => { return identity.wif;})
      .forEach(async (identity) => {
        if(this.state.message.identity.username_signature === identity.username_signature) {
          this.state.staticIdentity = true
          this.state.selectedIdentity = identity
          this.state.identity.identity = await this.state.identity.reviveUser(identity.wif, identity.username)
          this.selectIdentity(identity)
        }
      })
    }
    if(this.state.message?.identifier) {
      this.state.public_username = this.state.message.identifier
    }
  }

  selectIdentity = async (identity) => {
    this.setState({
      selectedIdentity: identity
    })
    let result = {
      identity: this.state.ci.toObject(identity)
    };
    if (this.state.method === 'getgraph') {
      await this.state.graph.refreshFriendsAndGroups()
      result.graph = this.state.graph.graph
    } else if (this.state.method === 'groups') {
    } else if (this.state.method === 'identity') {
    } else if (this.state.method === 'addcontact') {
      await this.state.graph.addFriend(this.state.message.contact)
      result.graph = this.state.graph.graph
    } else if (this.state.method === 'addgroup') {
      await this.state.graph.addGroup(this.state.message.group)
      result.graph = this.state.graph.graph
    } else if (this.state.method === 'collection') {
      await this.state.graph.refreshFriendsAndGroups()
      result.graph = this.state.graph.graph
    } else if (this.state.method === 'sendmail') {
      await this.state.graph.refreshFriendsAndGroups()
      await this.state.graph._sendMail(this.state.message)
    } else if (this.state.method === 'getmail') {
      await this.state.graph.refreshFriendsAndGroups()
      await this.state.graph._getMail(null, this.state.message.collection)
      result.graph = this.state.graph.graph
    }
    if (this.state.sign) return;
    this.respond(result)
  }

  respond(result) {
    let source;
    if(this.state.portal === 'window') {
      source = window.opener || window.parent
      if(source){
        const message = {}

      }
    } else if(this.state.portal === 'iframe') {
      source = window.parent
    }
    source.postMessage(
      {
        method: this.state.method,
        portal: this.state.portal,
        result: result
      },
      this.state.origin
    )
  }

  sign = async () => {
    let { selectedIdentity } = this.state;
    if (!selectedIdentity.key) {
      selectedIdentity = await this.state.ci.reviveUser(selectedIdentity.wif, selectedIdentity.username)
    }
    this.setState({
      signature: await this.state.ci.sign(this.state.message.challenge, selectedIdentity)
    })
    this.respond({
      identity: this.state.ci.toObject(selectedIdentity),
      hash: this.state.message.challenge,
      signature: this.state.signature,
    })
  }

  render() {
    console.log(RecoverIdentity)
    var extra_args = JSON.parse(localStorage.getItem('centeridentity_extra_args') || '{}');
    return (
      <Provider>
        <View style={styles.container}>
          <Text
            onPress={()=> {
              Linking.openURL("https://centeridentity.com");
            }}
          >
            <Image
              source={{uri: "https://centeridentity.com/centeridentitystatic/center-identity-logo.png"}}
              style={styles.logo}
            />
          </Text>
          {!this.state.selectedIdentity && this.state.identities.length === 0 && <Text style={styles.menuTitle}>Welcome to Center Identity. Please create a new identity clicking 'locate identity', entering a private usename, and clicking a secret location on the map twice.</Text>}
          {this.state.selectedIdentity && this.state.message?.challenge && <Text style={styles.menuTitle}>Use the identity saved for <strong>{this.state.selectedIdentity.username}</strong> to sign this message?</Text>}
          {this.state.selectedIdentity && this.state.message?.challenge &&  <Text>{this.state.message.challenge}</Text>}
          {this.state.error && <Text>{this.state.error}</Text>}
          <StatusBar style="auto" />
          {this.state.selectedIdentity && this.state.sign && <Button
            onPress={() => {this.sign()}}
          >Sign</Button>}
          {this.state.identities.length > 0 && this.state.selectedIdentity && !this.state.staticIdentity && <Button
              onPress={() => {this.setState({selectedIdentity: null, importIdentity: false, addIdentity: false, selectIdentity: true, exportIdentity: false})}}
            >Select different identity</Button>
          }
          {this.state.identities.length > 0 && !this.state.selectedIdentity && !this.state.staticIdentity && <Button
              onPress={() => {this.setState({selectIdentity: true, importIdentity: false, addIdentity: false, exportIdentity: false})}}
            >Select identity</Button>
          }
          {!this.state.selectedIdentity && !this.state.staticIdentity && <Button
              onPress={() => {this.setState({checked: true, selectedIdentity: null, importIdentity: false, addIdentity: true, selectIdentity: false, exportIdentity: false})}}
            >Locate identity</Button>
          }
          {!this.state.selectedIdentity && !this.state.staticIdentity && <Button
              onPress={() => {this.setState({checked: true, selectedIdentity: null, importIdentity: true, addIdentity: false, selectIdentity: false, exportIdentity: false})}}
            >Import identity</Button>
          }
          {this.state.identities.length > 0 && !this.state.staticIdentity && this.state.selectedIdentity && <Button
              onPress={() => {this.setState({importIdentity: false, addIdentity: false, selectIdentity: false, exportIdentity: true})}}
            >Export identity</Button>
          }
          {this.state.selectedIdentity && this.state.exportIdentity && <View>
              <TextInput
                label="WIF"
                value={this.state.selectedIdentity.wif}
              />
            </View>
          }
          {this.state.identities.length > 0 && !this.state.selectedIdentity && this.state.selectIdentity && <Text style={styles.menuTitle}>Select an identity from the list below</Text>}
          {this.state.identities.length > 0 && !this.state.selectedIdentity && this.state.selectIdentity && this.state.identities.sort((a, b) => {
            if (a.username.toLowerCase() > b.username.toLowerCase()) return 1
            if (a.username.toLowerCase() < b.username.toLowerCase()) return -1
            return 0
          }).map((identity) => {
            return <Button
              labelStyle={styles.username}
              onPress={() => {this.selectIdentity(identity)}}
            >{identity.username}</Button>
          })}
          {!this.state.selectedIdentity && this.state.addIdentity && <Text style={styles.menuTitle}>On the map below, find an exact location which is very personal / private to you and click it twice. This will be the information you use to recover your identity</Text>}
          {!this.state.selectedIdentity && this.state.importIdentity && <Text style={styles.menuTitle}>Input your public username and WIF key below</Text>}
          {!this.state.selectedIdentity && (this.state.addIdentity || this.state.importIdentity) && <Checkbox.Item
              label="Remember identity?"
              status={this.state.checked ? 'checked' : 'unchecked'}
              style={styles.checkbox}
              onPress={() => {
                this.setState({
                  checked: !this.state.checked
                })
              }}></Checkbox.Item>
          }
          {!this.state.selectedIdentity && this.state.importIdentity && <View>

              <TextInput
                label="Username"
                value={this.state.public_username || this.state.username}
                onChangeText={(username) => {this.setState({username: username})}}
              />
              <TextInput
                label="WIF"
                value={this.state.wif}
                onChangeText={(wif) => {this.setState({wif: wif})}}
              />
              <Button
                onPress={async () => {
                  const identity = await this.state.ci.reviveUser(this.state.wif, this.state.username)
                  this.state.identities.push(identity)

                  if (this.state.checked) {
                    localStorage.setItem('identities', JSON.stringify(this.state.identities.map((item) => {return JSON.parse(this.state.ci.toJson(item))})))
                  }
                  this.setState({importIdentity: false, selectIdentity: true})
                }}
              >Confirm</Button>
            </View>
          }
          {!this.state.selectedIdentity && this.state.addIdentity && <RecoverIdentity
            ci={this.state.ci}
            userNotFoundMessage={this.state.user_not_found_message || 'Identity not found for provided username and coordinates.'}
            publicUsername={this.state.public_username || ''}
            publicUsernameLabel={this.state.public_username_label || 'Public username'}
            privateUsernameLabel={this.state.private_username_label || 'Private username (do not share)'}
            userNotFoundMessage="No account found for that email address, please click 'create new' to create a new account."
            extraData={extra_args}
            onIdentity={async (identity) => {
              this.state.identities.push(identity)

              if (this.state.checked) {
                localStorage.setItem('identities', JSON.stringify(this.state.identities.map((item) => {return JSON.parse(this.state.ci.toJson(item))})))
              }

              this.setState({
                importIdentity: false,
                selectIdentity: false,
                selectedIdentity: identity,
                addIdentity: false
              })
              let result = {
                identity: this.state.ci.toObject(identity)
              };
              this.respond(result)
            }}
            createText={this.state.create_button_text || 'Create new'}
            tryAgainText={this.state.try_again_button_text || 'Try again'}
            width='100%'
            height={600}
            clientSideOnly={true}
          />}
        </View>
      </Provider>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    justifyContent: 'center',
    width: '100%',
    padding: 25,
    textAlign: 'center'
  },
  logo: {
    width: 400,
    height: 85
  },
  checkbox: {
    width: 250
  },
  menuTitle: {
    fontSize: 20,
    marginTop: 25,
    marginBottom: 10
  },
  username: {
    textTransform: 'none',
    color: '#1bb5d7',
    fontWeight: 'bold',
    fontSize: 20
  }
});
